import http from '../../common/http.js'
export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			noticeShow: true,
			eye: 'eye', //隐藏数据图标
			list: [{
					name: '用户中心'
				},
				{
					name: '工作台'
				}
			],
			current: 0,
			swiperCurrent: 0,
			tabsHeight: 0,
			noticeList: [
				"关于 xxxx产品完税订单更改地址更新说明 通知",
				"关于 xxxx产品完税订单更改地址更新说明 通知"
			],
		}
	},
	onLoad: function() {

		this.allRequest()
	},
	onShow: function() {

	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {

		},
		aa: function() {
			uni.showLoading({
				title: '加载中'
			})
			let opts = {
				url: '',
				method: 'post'
			};
			let param = {
				city_id: '370700',
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				uni.hideLoading()
			}, error => {
				console.log(error);
				uni.hideLoading()
			})
		},
		//关闭滚动通知
		closeNotice: function() {
			this.noticeShow = false
		},
		//点击小眼睛
		hideData: function() {
			console.log(5124)
			this.eye = this.eye == 'eye' ? 'eye-fill' : 'eye'
			console.log(this.eye)
		},
		goPage:function(e){
			uni.navigateTo({
				url: e
			})
		},


	
		// tab栏切换
		change(index) {
			this.swiperCurrent = index;

		},
		transition({
			detail: {
				dx
			}
		}) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({
			detail: {
				current
			}
		}) {
			this.$refs.tabs.setFinishCurrent(current);
			this.swiperCurrent = current;
			this.current = current;
		}


	}
}
